
import { defineComponent } from "vue";

export default defineComponent({
  name: "widget-14",
  props: {
    widgetClasses: String,
    widgetColor: String,
    data: Object,
    listValRef: Object,
  },
  data() {
    return {};
  },
  computed: {
    ies() {
      if (this.data)
        return (
          this.data.frequence_ir?.value *
          (this.data.gravite_dangerosite_ir?.value +
            this.data.gravite_persistance_ir?.value +
            this.data.gravite_etendu_ir?.value)
        );
      return 0;
    },
    iae_color() {
      if (this.ies > 20) {
        return "danger";
      } else if (this.ies > 10 && this.ies <= 20) {
        return "warning";
      } else {
        return "info";
      }
    },
    niveau() {
      if (this.listValRef) {
        if (this.ies >= 0 && this.ies <= 10) {
          return this.listValRef.niveau_priorites[2];
        } else if (this.ies > 10 && this.ies <= 20) {
          return this.listValRef.niveau_priorites[1];
        } else if (this.ies > 20) {
          return this.listValRef.niveau_priorites[0];
        }
      }
      return { name: "", action: "" };
    },
  },
});
